.center{
    font-family: 'Raleway', sans-serif;
    
    position: absolute;
    height: 100%;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    flex-direction: row;
   
}
.card{
    background-color: #fff;
   
    padding: 1rem;
    margin: 10px;
    width: 19rem;
    align-items: center;
    justify-content: center;
}

.card > form{
    display: flex;
    flex-direction: column;
}

.card h1{
    text-align: center;
    margin-top: 0;
    margin-bottom: 10px;
}

.form-item{
    font-family: 'Raleway', sans-serif;
    padding: 5px;
    margin-bottom: 1rem;
    height: 30px;
    width: 16rem;
    border: 1px solid grey;
    padding-left: 10px;
}

.error {
    background-color: red;
  }

.form-submit{
    font-family: 'Raleway', sans-serif;
    height: 35px;
    color: #fff;
    background-color: #bc1a93;
    border: none;
    letter-spacing: 0.2rem;
    transition: 0.3s opacity ease;
    cursor: pointer;
    
    margin: 20px;
}
.form-submit:hover{
   opacity: 0.6;
}

